import React from 'react';
import {CanvasComponent} from "./CanvasComponent";
import {EditorTextComponent} from "./EditorTextComponent";
import {connect} from "react-redux";

const mapStateToProps = state => {
    return {loading: state.loading};
};

class IdeComponent extends React.Component {

    constructor(props, context) {
        super(props, context);
    }

    render() {
        return (<div className={"col-9 canvas-container" + (this.props.loading ? " loading" : "")}>
            <EditorTextComponent/>
            <CanvasComponent/>
        </div>);
    }
}

const RIdeComponent = connect(mapStateToProps)(IdeComponent);

export {RIdeComponent as IdeComponent}
