import defaultState, {getFlattenProperties} from "./store/defaultJsonOptions";
import store from "./store/store";

const LAYERS_ORDER = Object.keys(getFlattenProperties(defaultState, "")).sort((a, b) => {
    let aIsForeground = a.toLowerCase().includes("foreground");
    let bIsForeground = b.toLowerCase().includes("foreground");
    if (aIsForeground && !bIsForeground) return 1;
    if (!aIsForeground && bIsForeground) return -1;
    return a < b ? -1 :
           a > b ? 1 :
           0;
});

function updateColor(prevColor, newColor, newAlpha) {
    if (newAlpha === 255) return newColor;
    return Math.min((prevColor * (255 - newAlpha) + newColor * newAlpha) / 255, 255);
}


class CanvasRenderer {

    constructor(width, height) {
        this.width = width;
        this.height = height;
        this.loaded = 1;

        const masks = {};
        for (let key of LAYERS_ORDER) {
            this.loaded++;
            let offscreenCanvas = new OffscreenCanvas(width, height);
            let img = new Image();
            img.addEventListener("load", () => {
                let context = offscreenCanvas.getContext("2d");
                context.drawImage(img, 0, 0);
                let data = context.getImageData(0, 0, width, height).data;
                let imageArray = new Uint8ClampedArray(this.width * this.height);
                for (let x = 0; x < this.width; x++) {
                    for (let y = 0; y < this.height; y++) {
                        let i = y * this.width + x;
                        imageArray[i] = 255 - data[i * 4];
                    }
                }
                masks[key] = imageArray;
                this.updateStateIfPossible();
            }, false);
            img.src = 'canvas-data/' + key + ".png";
        }
        /** @type {Object<String, Uint8ClampedArray>} */
        this.layerCanvas = masks;

        let frontLayer = new Image();
        frontLayer.addEventListener("load", () => {
            this.frontLayer = frontLayer;
            this.updateStateIfPossible();
        }, false);
        frontLayer.src = 'canvas-data/FrontLayer.png';
    }

    updateStateIfPossible() {
        this.loaded--;
        if (this.loaded === 0) {
            store.dispatch({type: "reload"})
        }
    }

    /**
     * @param {CanvasRenderingContext2D} ctx
     * @param state
     */
    render(ctx, state) {
        let imageArray = new Uint8ClampedArray(this.width * this.height * 4);

        for (let key of LAYERS_ORDER) {
            let mask = this.layerCanvas[key];
            if (!mask) continue;

            let indexOfFirstDot = key.indexOf(".");
            let color = state[key.substr(0, indexOfFirstDot)][key.substr(indexOfFirstDot + 1)];

            for (let x = 0; x < this.width; x++) {
                for (let y = 0; y < this.height; y++) {
                    let i = y * this.width + x;
                    if (mask[i] > 0) {
                        let i4 = i * 4;
                        imageArray[i4] = updateColor(imageArray[i4], parseInt(color.substr(1, 2), 16), mask[i]);
                        imageArray[i4 + 1] = updateColor(imageArray[i4 + 1], parseInt(color.substr(3, 2), 16), mask[i]);
                        imageArray[i4 + 2] = updateColor(imageArray[i4 + 2], parseInt(color.substr(5, 2), 16), mask[i]);
                        imageArray[i4 + 3] = 255;
                    }
                }
            }
        }

        let imgData = new ImageData(imageArray, this.width, this.height);

        createImageBitmap(imgData).then(imgBitmap => {
            ctx.drawImage(imgBitmap, 0, 0);
            if (this.frontLayer) {
                ctx.drawImage(this.frontLayer, 0, 0);
            }
        });
    }
}


function getProperty(state, property) {
    let split = property.split(".");
    let optionsProperty = getValueBySplitQName(state, split);
    let defaultProperty = getValueBySplitQName(defaultState, split);
    if (optionsProperty !== defaultProperty) return optionsProperty;

    let optionsStarProperty = state["*"][split[split.length - 1]];
    if (optionsStarProperty) return optionsStarProperty;

    return defaultProperty;
}

function getValueBySplitQName(options, split) {
    let value = options;
    for (let part of split) {
        value = value[part];
    }
    return value;
}

export default CanvasRenderer;
